$(document).ready(function() {
    $('.scrollTo').click( function() { // Au clic sur un élément
            var page = $(this).attr('href'); // Page cible
            var speed = 750; // Durée de l'animation (en ms)
            $('html, body').animate( { scrollTop: $(page).offset().top }, speed ); // Go
            return false;});
            });

    $(document).ready(function(){ // pour l'ouverture du menu
      const toggle = document.querySelector('#menu')
      const visible = document.querySelector('#collapse')

      toggle.addEventListener('click',()=>{
        visible.classList.toggle('collapse')
      })
    });

$(document).ready(function(){$(".hide_menu").click(function(){$("#menu").trigger("click")})}) // pour la fermeture apres click du menu

      $(document).ready(function(){ // pour l'animation du cercle du bouton principal
        const add = document.querySelector('#declenche_cercle')
        const visible = document.querySelector('#anim_cercle')

        add.addEventListener('mouseenter',()=>{
          visible.classList.add('anim_cercle')
        })

        const remove2 = document.querySelector('#declenche_cercle')
        const visible3 = document.querySelector('#anim_cercle')

        remove2.addEventListener('mouseleave',()=>{
        visible3.classList.add('anim_cercle2')
        visible3.classList.remove('anim_cercle')

        setTimeout(function(){visible3.classList.remove('anim_cercle2')}, 380);
        })
      });


      $(document).ready(function(){ // pour l'animation de la fleche

        const line = document.querySelector('.declenche_line')
        const line_red = document.querySelector('#line_red')

        line.addEventListener('mouseenter',()=>{
        line_red.classList.add('line_red3')
        })
        const line_red2 = document.querySelector('.declenche_line')
        const line_red3 = document.querySelector('#line_red')

        line_red2.addEventListener('mouseleave',()=>{
        line_red3.classList.add('line_red2')
        line_red3.classList.remove('line_red3')

        setTimeout(function(){line_red3.classList.remove('line_red2')}, 400);
        })
      });


const cursor = document.querySelector(".c-cursor"); // pour le curseur personnalise
const body = document.querySelector(".body");

body.addEventListener("mousemove", (e) => {
  cursor.style.left = e.pageX + "px";
  cursor.style.top = e.pageY + "px";
  cursor.setAttribute("data-fromTop", cursor.offsetTop - scrollY);
});

window.addEventListener("scroll", () => { 
  const fromTop = cursor.getAttribute("data-fromTop");
  cursor.style.top = scrollY + parseInt(fromTop) + "px";
});



body.addEventListener("click", () => { // pour l'animation au click du curseur personnalise
  cursor.classList.add("__circle")
  setTimeout(function(){cursor.classList.remove("__circle")}, 500);
});


document.addEventListener('DOMContentLoaded', function () { // pour la fonction hover du curseur personnalise - x5 pour les 5 projets 
  var ancre_projet = document.querySelector('.vers_projet');
  var curseur = document.querySelector('.c-cursor');

  ancre_projet.addEventListener('mouseenter', function (e) {
      curseur.classList.add('is-hover');
  });
  ancre_projet.addEventListener('mouseleave', function () {
      curseur.classList.remove('is-hover');
  })
})

